<template>
	<!-- echarts示例 -->
	<view class="container">
		<div class="box"><my-echarts id="main" ref="mapChart" :echarts="echarts" :onInit="initChart" /></div>
	</view>
</template>

<script>
import * as echarts from '@/common/echarts.min.js';
import myEcharts from '@/components/src/echarts.vue';

//
export default {
	components: { myEcharts },
	data() {
		return { echarts };
	},
	methods: {
		//初始化
		initChart(canvas, width, height) {
			let chart = null;
			chart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			var option = {
				tooltip: {
					trigger: 'axis',
					// formatter:(item)=>{
					//   let txtCon = `(${item.data.value}℃)`;
					//   return txtCon
					// },

					axisPointer: {
						type: 'shadow',
						label: {
							show: false
							// formatter: '{value}℃'//{value} 会被自动替换为轴的值
						},
						shadowStyle: {
							color: 'rgba(253, 246, 218,0.8)'
						}
					}
				},
				grid: {
					left: '3%',
					right: '4%',
					bottom: '3%',
					containLabel: true
				},
				xAxis: [
					{
						type: 'category',
						data: ['0:00', '4:00', '8:00', '12:00', '16:00', '20:00', '24:00'],
						axisTick: {
							alignWithLabel: true
						}
					}
				],
				yAxis: [
					{
						type: 'value',
						max: 60, //最大值
						min: 0, //最小值
						interval: 30, //间隔
						axisLabel: {
							//数字显示
							color: '#afafbc',
							formatter: function(val) {
								//百分比显示
								return val + '℃';
							}
						},
						axisTick: {
							alignWithLabel: true
						}
					}
				],
				series: [
					{
						// name: 'Direct',
						type: 'bar',
						barWidth: '35%',
						data: [10, 42, 20, 33, 39, 30, 22],
						itemStyle: {
							color: '#4474ff',
							// 移入当前的柱状图时改变颜色
							emphasis: {
								color: '#e3b800'
							}
						}
						// label: {//数值展示在头顶
						//     show: true,
						//     position: "top"
						//   }
					}
				]
			};

			chart.setOption(option);

			return chart; // 返回 chart 后可以自动绑定触摸操作
		}
	}
};
</script>
<style>
.box {
	width: 100%;
	height: 540rpx;
	/* background-color: pink; */
}
#main {
	width: 100%;
	height: 100%;
}
</style>
